<script>
import QRCode from 'qrcode'
  export default {
    name: 'Pay',
    data(){
      return{
        payInfo:{},
        timer:null,
        code:'',
      }
    },
    computed:{
      orderCode(){
        return this.$route.query.orderId
      }
    },
    methods:{
      // 
      async getOrderment(){
        let result =  await this.$API.reqPayment(this.orderCode)
        if(result.code==200){
          this.payInfo = result.data
        }
      },
      async open() {
        // qrcode
        let url = await QRCode.toDataURL(this.payInfo.codeUrl)
        console.log(this.payInfo.codeUrl)
        this.$alert(`<img src=${url} />`, '', {
          //  message  HTML 
          dangerouslyUseHTMLString: true,
          // 
          showCancelButton:false,
          // 
          center:true,
          // 
          showCancelButton:true,
          // 
          cancelButtonText:'',
          // 
          confirmButtonText:'',
          beforeClose:(type,instance,done)=>{
            if(type=='cancel'){
              alert('well Everything is will be ok');
              clearInterval(this.timer);
              this.timer = null;
              done()
            }else{
              // if(this.code == 200){
                clearInterval(this.timer)
                this.timer = null;
                done()
                this.$router.push('/paysuccess')
              /* }else{
                alert('...')
              } */
            }
          },
        }).catch(err=>{});
            // 
          if(!this.timer){
            //  
            this.timer = setInterval(()=>{
              let result = this.$API.reqPayStatus(this.orderCode)
              // 200
              if(result.code == 200){
                // 
                clearInterval(this.timer)
                // null
                this.timer = null
                // code
                this.code = result.code
                // 
                this.$msgbox.close();
                // 
                this.$router.push('/paysuccess')
              }
            },1000)
          }
      }
    },
    mounted(){
      this.getOrderment()
    }
  }
</script>

