              >
                <ul class="unstyled">
                  <li>{{ item.totalAmount }}.00</li>
                  <li></li>
                </ul>
              </td>
              <td
                :rowspan="item.orderDetailList.length"
                v-if="inedex == 0"
                width="8%"
                class="center"
              >
                <a href="#" class="btn">{{ item.orderStatusName }} </a>
              </td>
              <td
                :rowspan="item.orderDetailList.length"
                v-if="inedex == 0"
                width="13%"
                class="center"
              >
                <ul class="unstyled">
                  <li>
                    <a href="mycomment.html" target="_blank">|</a>
                  </li>
                </ul>
