</template>

<script>
export default {
  name: "MyOrder",
  data() {
    return {
      //   
      // 
      page: 1,
      // 
      limit: 3,
      orderList: {},
    };
  },
  methods: {
    //   
    async getListData() {
      let result = await this.$API.reqMyOrderList(this.page, this.limit);
      console.log(result);
      if (result.code == 200) {
        this.orderList = result.data;
      } else {
        alert("");
      }
    },
    // 
    getPageNo(page){
      this.page = page
      this.getListData();
    }
  },
  mounted() {
    // 
    this.getListData();
  },
};
</script>

