<template>
  <div class="pagination">
    <button :disabled='pageNo==1' @click="$emit('getPageNo',pageNo-1)"></button>
    <button v-if="startNumAndEndNum.start>1" @click="$emit('getPageNo',1)" :class="{active:pageNo==1}">1</button>
    <button v-if="startNumAndEndNum.start>2"></button>
    <!--  -->
    <template v-for="(page,index) in startNumAndEndNum.end" >
      <button :key="index" v-if='page >= startNumAndEndNum.start' @click="$emit('getPageNo',page)" :class="{active:pageNo==page}">{{page}}</button>
    </template>
    <button v-if="startNumAndEndNum.end<totalPage-1"></button>
    <button v-if="startNumAndEndNum.end<totalPage" @click="$emit('getPageNo',totalPage)" :class="{active:pageNo==totalPage}">{{totalPage}}</button>
    <button :disabled='pageNo==totalPage' @click="$emit('getPageNo',pageNo+1)"></button>

    <button style="margin-left: 30px"> {{total}} </button>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  data() {
    return {}
  },
  props:['pageNo','pageSize','total','continues'],
  computed:{
    // 
    totalPage(){
      // 
      return Math.ceil(this.total/this.pageSize);
    },
    startNumAndEndNum(){
      // 
      const{pageNo,totalPage,continues} = this;
      // start|end
      let start =0, end =0;
      //   
      if(continues>totalPage){
        // 1
        start = 1,
        // 
        end = totalPage
        //      
      }else{
        //   ()
        start = pageNo-parseInt(continues/2);
        // 
        end = pageNo+parseInt(continues/2);
        //  1
        if(start<1){
          // 1
          start = 1,
          // 
          end = continues
        }
        //  
        if(end>totalPage){
          //     +1
          start = totalPage-continues+1,
          end = totalPage
        }
      }
      return {start,end};
    }
  },
  mounted() {
    
  },
};
</script>

<style lang="less" scoped>
.pagination {
  text-align: center;
  button {
    margin: 0 5px;
    background-color: #f4f4f5;
    color: #606266;
    outline: none;
    border-radius: 2px;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;

    &[disabled] {
      color: #c0c4cc;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background-color: tomato;
      color: #fff;
    }
  }
}
</style>