<script>
import { mapState } from "vuex";
export default {
  name: "Register",
  data() {
    return {
      phone: "",
      password: "",
      password1: "",
      agree: true,
    };
  },
  methods: {
    // 
    async sendInspectCode() {
      try {
        if (this.phone) {
          await this.$store.dispatch("getInspectCode", this.phone);
        } else {
          alert("");
        }
        // this.code = this.$store.state.user.code  code    data
      } catch (error) {
        alert(error.message);
      }
    },
    async userRegister() {
      const success = await this.$validator.validateAll();
      if(success){
        try {
          const { phone, code, password } = this;
            let result = await this.$store.dispatch("userRegister", {phone,code,password,});
          this.$router.push("/login");
        } catch (error) {
          alert(error.message);
        }
      }
    },
  },
  computed: {
    ...mapState({
      code() {
        return this.$store.state.user.inspectcode;
      },
    }),
  },
  mounted() {
    // console.log(this.inspectCode)
  },
};
</script>

